<template>
    <yd-layout class="box_correct">
        <navBar title="随手拍隐患整改" slot="navbar"></navBar>
        <div class="box_form">
            <div class="box_cont" style="align-items: center;">
                <p class="mright">金额：</p>
                <input v-on:focus="heighOn" type="number" class="box_input" placeholder="请输入整改金额(元)" v-model="money">
            </div>
            <div class="box_cont mtop">
                <p class="mright">备注：</p>
                <textarea v-on:focus="heighOn" rows="5" class="box_txt" placeholder="请输入备注" v-model="remarks"></textarea>
            </div>
            <div class="box_cont mtop">
                <p class="mright">附件：</p>
                <unphoto v-on:imagesUrl="saveUrl"></unphoto>
                <!--<input type="file">-->
            </div>
            <yd-button @click.native="saveData" size="large" color="#fff" bgcolor="#1D91E6">保存</yd-button>
        </div>
    </yd-layout>
</template>

<script>
    import {api_camera_correct} from "../../components/APIKeys";
    import unphoto from '../../components/unphoto.vue'
    export default {
        name: "CameraCorrect",
        components:{unphoto},
        data(){
            return {
                money:null,
                remarks:'',
                photoUrl:'',//保存子组件传过来的base64
                // token:window.localStorage.getItem('token'),
                ssp_id:window.localStorage.getItem('ssp_id'),
            }
        },
        mounted(){

        },
        methods:{
            saveUrl:function(data){
                this.photoUrl = data;
            },
            //点击保存
            saveData(){
                if(this.money == '' || this.money == null){
                    this.$showError('请填写整改金额');
                    return false;
                }else if(this.remarks == ''){
                    this.$showError('请填写备注');
                    return false;
                }else if(this.photoUrl.length == 0){
                    this.$showError('请上传整改图片');
                    return false;
                }
                this.$showDialog();
                let jsonData = {
                    // token:this.token,
                    ssp_id:this.ssp_id,
                    zg_money:this.money,
                    zg_remarks:this.remarks,
                    attachment:this.photoUrl,
                };
                this.$post(api_camera_correct, jsonData).then(res=>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        this.$showSuccess('保存成功');
                        window.history.go(-2);
                    }else{
                        this.$showError(res.errmsg);
                    }
                });
            },
            //防止键盘遮挡
            heighOn: function() {
                if (/Android/gi.test(navigator.userAgent)) {
                    window.addEventListener('resize', function () {
                        if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
                            window.setTimeout(function () {
                                document.activeElement.scrollIntoViewIfNeeded();
                            }, 0);
                        }
                    })
                }
            },
        }
    }
</script>

<style scoped>
    .box_correct{
        background: #fff;
    }
    .box_form{
        padding: .3rem;
    }
    .box_cont{
        display: flex;
        /*align-items: center;*/
    }
    .box_input{
        height: 0.8rem;
        flex: 1;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        border-radius:5px;
    }
    .box_txt{
        flex: 1;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        padding: .2rem;
    }
    .mtop{
        margin-top: .2rem;
    }
    .mright{
        margin-right: .2rem;
        white-space:nowrap;
    }
    .box_light{
        flex: 1;
        display: flex;
        align-items: center;
        flex-wrap:wrap;
    }
    .box_img{
        width: calc(95% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        flex-direction:column;
        align-items: center;
        text-align: center;
        border: 1px solid #d9d9d9;
        padding: .2rem;
        margin-bottom: .1rem;

    }
    .box_img img{
        width: 60%;
        height:60%;
    }
    .box_photo{
        width: calc(93% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        margin-bottom: .1rem;
        position: relative;
    }
    .box_photo .img1{
        width: 100%;
        height: 100%;
    }
    .box_delete{
        position: absolute;
        right: 0;
        top: 0;
        width: 35%;
        height: 35%;
    }
</style>



